/* horizontal Cards */

.horizontalCardLeftProject{
  display: grid;
  grid-template-rows: 25% auto 25%;
}


.horizontalCardRightProject{
  display: grid;
  grid-template-rows: 15% 15% 1fr auto;
  border-left: 2px solid grey;
}

.horizontalCard {
  margin: 0.7em;
  display: grid;
  grid-template-columns: 30% auto;
  padding: 0.5em;
  border-left: 2px solid green;
  border: 1px solid grey;
  box-shadow: /* 0px 2px .5em black; */
          5px 2px .7em black;
}

.horizontalCardLeft {
  display: grid;
  grid-template-rows: auto 25%;
}

.horizontalCardRight {
  display: grid;
  grid-template-rows: 15% 15% 1fr 1fr auto;
  border-left: 2px solid grey;
  padding-left: 1em;
  text-align: left;
}

