.noMargin {
  margin: 0;
}

#navbarNavDropdown>ul {
  justify-content: space-between;
}

#root > div > div:nth-child(1) > nav.navbar.navbar-expand.navbar-light.bg-light {
  padding-left: 0.1em;
  padding-right: 0.1em;
}

.view {
  margin: auto;
  width: 100%;
}

.wrapper {
  position: relative;
  overflow: auto;
  /* border: 1px solid black; */
  white-space: nowrap;
}

.sticky-col {
  position: -webkit-sticky;
  position: sticky;
  padding: 1em;
  background-color: #f8f9fa!important;
}

.first-col {
  width: 100px;
  min-width: 100px;
  max-width: 100px;
  left: 0px;
}

.second-col {
  width: 150px;
  min-width: 150px;
  max-width: 150px;
  left: 100px;
}

/* Search */

.searchField{
  position: relative;
  z-index: 3;
  width: 20em;
  text-align: center;
}

.searchResults{
  width: 20em;
  list-style: none;
  background-color: white;
  z-index: 4;
}

.searchResults li{
  list-style: none;
  border-bottom: 2px solid black;
  text-align: left;
  padding: 0.5em;
}

.searchResults li:hover{
  background-color: lightgrey;
}

.searchResults ul{
  padding-left: 0;
}

.focusCatch {
  position: absolute;
left: -500%;
    right: -500%;
    height: 200vh;
  margin-top: -200px;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.4);
}

.desktopSearch {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
  width: 20em;
}

/* upload image */
#imgViewer{
  max-width: 250px;
}


.textJustify{
  text-align: justify;
}

.card-img-top{
  content: "";
  background-size: cover!important;
  background-repeat: no-repeat!important;
  background-position-x: center;
  min-height: 200px;
  max-height: 200px;
  min-width: 100%;
}